import React, { useState } from 'react';
import { Modal, Button ,Alert} from 'react-bootstrap';
import PlaylistsTable from './PlaylistsTable';
import Form from 'react-bootstrap/Form';
import Toast from 'react-bootstrap/Toast';
import Spinner from 'react-bootstrap/Spinner';

function PlaylistSaveModal(props) {
  const [showModal, setShowModal] = useState(false);

  const handleClose = () => setShowModal(false);
  const handleShow = () => setShowModal(true);
  const [validated, setValidated] = useState(false);

  const [playlistName, setPlaylistName] = useState('');
  const [error, setError] = useState('');
  const [showToast, setShowToast] = useState(false);
  const [loading, setLoading] = useState(false);

  const handleChange = (event) => {
    const inputValue = event.target.value; // Remove leading/trailing spaces
    const regex = /^[\w\-\s]*$/; // Allow only alphanumeric and spaces
    if (inputValue.length > 50) {
      setError('Playlist name cannot exceed 50 characters.');
    } else if (!regex.test(inputValue)) {
      setError('Playlist name can only contain alphanumeric characters and spaces.'+inputValue+"#");
    } else {
      setError('');
      setPlaylistName(inputValue);
    }
  };
  

  const handleSubmit = (event) => {
    event.preventDefault();
    if (playlistName.length > 0 && error === '') {
      handleSave(playlistName);
      //alert(`Playlist name is: ${playlistName}`);
    }
  };

  function extractIds(items) {
    const ids = [];
  
    for (let i = 0; i < items.length; i++) {
      const item = items[i];
      if (item.hasOwnProperty("id")) {
        ids.push(item.id);
      }
    }
  
    return ids;
  }

  const handleSave = (aPlaylistName) => {
    const idList=extractIds(props.list);
    //console.log(idList);
    const fetchData = async () => {
      try {
      const data = {
        playlistName: aPlaylistName,
        list: extractIds(props.list)
      };

     setLoading(true);
      const response = await fetch('/saveplaylistx', 
      {method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
       body: JSON.stringify(data)});
       console.log("data sent:");
       console.log(data);
       
      const jsonData = await response.json();
      setLoading(false);
      if (response.status === 200) {
        //alert("Success!");
        console.log(response);
        setShowToast(true);

        setTimeout(() => setShowToast(false), 1000); // Hide the toast after 3 seconds.

        setTimeout(() => {
          handleClose();
        }, 3000);
        
        //handleClose();
      } else {
        alert(`Error saving`);
        console.log("error saving");
        console.log(jsonData);
        console.log(response);
      }
    } catch (error) {
      alert(`Error saving exception`);
      setLoading(false);
      console.error(error);
      
      // handle the error here
    }
//      setIsLoading(false);
    };
    fetchData();

    console.log("Save called");
  };


  return (
    <>
      <Button variant="success" onClick={handleShow}>
        {props.linkText}
      </Button>

      <Modal show={showModal} onHide={handleClose} size="sm">
        <Modal.Header closeButton>
          <Modal.Title>{props.modalTitle}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          Enter the playlist name and hit submit to save the playlist on Spotify
          <br />
          <br />
          <Form onSubmit={handleSubmit}>
            <Form.Group controlId="formPlaylistName">
              <Form.Label>Playlist Name:</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter playlist name"
                value={playlistName}
                onChange={handleChange}
              />
              {error && <Alert variant="danger">{error}</Alert>}
            </Form.Group>
          </Form>
          
          <Toast
            show={showToast}
            onClose={() => setShowToast(false)}
            delay={1000}
            autohide
            align="center"
          >
            <Toast.Header>
              <strong className="me-auto">Success</strong>
            </Toast.Header>
            <Toast.Body style={{ color: "black" }}>
              Playlist saved to Spotify
            </Toast.Body>
          </Toast>
        </Modal.Body>
        <Modal.Footer>
          {loading ? (
            <div
              className="d-flex justify-content-center align-items-center"
              style={{ height: "400px" }}
            >
              <Spinner animation="border" variant="primary" />
            </div>
          ) : (
            <div></div>
          )}
          <Button variant="secondary" onClick={handleClose}>
            Cancel
          </Button>{" "}
          {/* This is the cancel button */}
          <Button variant="primary" type="submit" onClick={handleSubmit}>
            Submit
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default PlaylistSaveModal;
