import React from 'react';

function ObfuscatedEmail() {
  const email = 'songtorun@gmail.com';
  const obfuscatedEmail = email.replace(/./g, function(char) {
    return "&#" + char.charCodeAt(0) + ";";
  });

  return (
    <p>If you have any questions or comments, please feel free to <a href={`mailto:${email}`} dangerouslySetInnerHTML={{__html: obfuscatedEmail}}></a>.</p>
  );
}

export default ObfuscatedEmail;
