import React, { createContext, useState } from 'react';

// Create the context
export const GlobalStateContext = createContext();

// Define the provider component
export const GlobalStateProvider = ({ children }) => {
  const [globalList, setGlobalList] = useState([]);

  const addToList = (newItem) => {
    setGlobalList([...globalList, newItem]);
  };

  const addListToGlobalList = (newList) => {
    setGlobalList([...globalList, ...newList]);
  };

  const clearGlobalList = () => {
    setGlobalList([]);
  };
  return (
    <GlobalStateContext.Provider value={{ globalList, addToList, addListToGlobalList,clearGlobalList }}>
      {children}
    </GlobalStateContext.Provider>
  );
};
