import React, { useState, useEffect, useRef } from 'react';
import * as d3 from 'd3';
import Spinner from 'react-bootstrap/Spinner';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';

import LinkModal from './LinkModal';
import Squeeze from './Squeeze';

const PlaylistTable = (props) => {
  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const { listid } = props.list_id;
  const svgRef = useRef(null);
  const handleDelete = (index) => {
    const newSongs = [...data];
    newSongs.splice(index, 1);
    setData(newSongs);
  };


  useEffect(() => {
    
    const fetchData = async () => {
      try{
      setIsLoading(true);
      const response = await fetch(`/playlistx?list_id=${props.list_id}`);
      if (response.status === 401) {
        // Redirect the user to the login page or display an error message
        window.location.href = '/';
        return;
      }
      const jsonData = await response.json();
      setData(jsonData);
      setIsLoading(false);
      }catch (error) {
        console.error(error);
        // Handle other errors here
        alert("error retrieving data");
        window.location.href = '/';
          return;
      }
    };
    fetchData();
  }, []);

  const handleCloseModal = () => {
    props.modalCloser();
  };

  const totalDurationInSeconds = data.reduce((acc, song) => acc + song.duration_s, 0);
  const totalDurationInMinutes = Math.round(totalDurationInSeconds / 60);

 return (
    <div className="container" style={{ backgroundColor: 'black', color: 'white' }}>
      <h1 className="cover-heading" style={{ color: 'steelblue' }}>Filter or sort the songs to be added  </h1>
 
      {isLoading ? (
        <Spinner animation="border" role="status">
          <span className="visually-hidden">Loading...</span>
        </Spinner>
      ) : (
        <>
       
          <Squeeze list={data} closingModal={handleCloseModal}/>

        </>
      )}
    </div>
  );
};




export default PlaylistTable;
