import React, { useEffect, useState } from 'react';
import * as d3 from 'd3';
import cloud from 'd3-cloud';
import Spinner from 'react-bootstrap/Spinner';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import PlaylistTableModal from './PlaylistTableModal';
import Dropdown from 'react-bootstrap/Dropdown';
import DropdownButton from 'react-bootstrap/DropdownButton';

const PlaylistsDropdown = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('/playlistsx');
        if (response.status === 401) {
          // Redirect the user to the login page or display an error message
          window.location.href = '/';
          return;
        }
        if (response.status === 500) {
          // Redirect the user to the login page or display an error message
          alert("Server error");
          window.location.href = '/';
          return;
        }
        const jsonData = await response.json();
        setData(jsonData);
        setLoading(false);
      } catch (error) {
        console.error(error);
        // Handle other errors here
        alert("error retrieving data");
        window.location.href = '/';
          return;
      }
    };
  
    fetchData();
  }, []);
  
 
  return (
  
  <div className="container">

      {loading ? (
        <div className="d-flex justify-content-center align-items-center" style={{ height: '400px' }}>
          <Spinner animation="border" variant="primary" />
        </div>
      ) : (
        <div>
        <DropdownButton id="dropdown-basic-button" title="Load One of your playlists">
          {data.map((d, i) => (
            <Dropdown.Item href="#/action-{i}" key={d.id}> 
              <PlaylistTableModal linkText={d.name} list_id={d.id} list_name={d.name} modalTitle={"Playlist:"+d.name} width="90%" height="90%" />
              ({d.numberOfTracks})
            </Dropdown.Item>
            ))}
      </DropdownButton>

        

            </div>
      )}
    </div>
  );
};

export default PlaylistsDropdown;
