import React, { useState } from 'react';
import { Modal, Button } from 'react-bootstrap';
import PlaylistTable from './PlaylistTable';

function PlaylistTableModal(props) {
  const [showModal, setShowModal] = useState(false);

  const handleClose = () => setShowModal(false);
  const handleShow = () => setShowModal(true);

  if (!props.list_id) {
    return null; // If url is empty, don't render anything
  }

  return (
    <>
      <a variant="primary" href='#' onClick={handleShow} style={{ fontSize: "20px" }}>
        {props.linkText}
      </a>

      <Modal show={showModal} onHide={handleClose} size="lg">
        <Modal.Header closeButton>
          <Modal.Title>{props.modalTitle}</Modal.Title>
        </Modal.Header>
        <Modal.Body>

          <PlaylistTable list_id={props.list_id} list_name={props.list_name} modalCloser={handleClose}/>

        </Modal.Body>
      </Modal>
    </>
  );
}

export default PlaylistTableModal;
